.header {
    display: flex;
    margin: 0 auto;
    max-width: 880px;
    width: 100%;
    padding: 45px 0 41px;
    border-bottom: 1px solid rgba(84, 84, 84, 0.7);
    flex: 0 0 auto;
}

@media screen and (max-width: 600px) {
    .header {
       padding: 28px 0 31px;
       margin: 0;
    }
}
