.profile__avatar {
    position: relative;
    object-fit: cover;
    width: 120px;
    height: 120px;
    border-radius: 50%;
}

.profile__avatar:hover::before {
    content: "";
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: black;
    opacity: 0.8;
  }
